<template>
    <div class="seller" ref="seller">
    	<div class="seller-content">
    		<div class="overview">
    			<h1 class="title">
    				{{seller.name}}
    			</h1>
    			<div class="desc border-1px">
    				<star :size="36" :score="seller.score"></star>
    				<span class="text">({{seller.ratingCount}})</span>
    				<span class="text">月售{{seller.sellCount}}单</span>
    			</div>
    			<ul class="remark">
    				<li class="block">
    					<h2>起送价</h2>
    					<div class="content">
    						<span class="stress">
    							{{seller.minPrice}}元
    						</span>
    					</div>
    				</li>
    				<li class="block">
    					<h2>商家配送</h2>
    					<div class="content">
    						<span class="stress">
    							{{seller.deliveryPrice}}元
    						</span>
    					</div>
    				</li>
    				<li class="block">
    					<h2>平均配送时间</h2>
    					<div class="content">
    						<span class="stress">
    							{{seller.deliveryTime}}元
    						</span>
    					</div>
    				</li>
    			</ul>
    			<div class="favorite" @click="toggleFavorite">
    				<span class="icon-favorite" :class="{'active':favorite}">
    					
    				</span>
    				<span class="text">
    					{{favoriteText}}
    				</span>
    			</div>
    		</div>
    		<split></split>
			<div class="bulletin">
				<h1 class="title">
					公告与活动
				</h1>
				<div class="content-wrapper">
					<p class="content">
						{{seller.bulletin}}
					</p>
				</div>
				<ul class="supports" v-if="seller.supports">
                        <li class="support-item border-1px" v-for="(item,index) in seller.supports">
                            <span class="icon" :class="classMap[seller.supports[index].type]">
                            </span>
                            <span class="text">
                                {{seller.supports[index].description}}
                            </span>
                        </li>
                    </ul>
			</div>
			<split></split>
			<div class="pics">
				<h1 class="title">
					商家实景
				</h1>
				<div class="pic-wrapper" ref="picWrapper">
					<ul class="pic-list" ref="picList">
						<li class="pic-item" v-for="pic in seller.pics">
							<img :src="pic" width="120" height="90">
						</li>
					</ul>
				</div>
			</div>
			<split></split>
			<div class="info">
				<h1 class="title">
					商家信息
				</h1>
				<ul>
					<li class="info-item" v-for="info in seller.infos">
						{{info}}
					</li>
				</ul>
			</div>
    	</div>
    </div>
</template>

<script type="ecmascript-6">
	import {mapState} from 'vuex';
    import {urlParse} from "../js/util.js";
	import store from "../store.js";
	import star from "./star.vue";
	import split from "./split.vue";
	import BScroll from "better-scroll";
    export default{
        props:['foodCont'],
    	data(){
    		return{
    			seller:{
    				id:(()=>{
    					let queryParam=urlParse();
    					return queryParam.id;
    				})()
    			},
    			favorite:false
    		}
    	},
    	computed:{
            ...mapState({
                classMap:state=>state.classMap
            }),
            favoriteText(){
            	return this.favorite? '已收藏':'未收藏';
            }
        },
        watch:{
        	'seller'(){
        		this.initScroll();
        		this.initPics();
        	}
        },
        mounted(){
                    this.seller=this.foodCont.seller;
                    this.initScroll();
                    this.initPics();
        },
        methods:{
        	toggleFavorite(event){
        		this.favorite=!this.favorite;
        	},
        	initScroll(){
        		if(!this.scroll){
	                this.scroll = new BScroll(this.$refs.seller, {
	                  click: true
	                })
	              }else{
	                	this.scroll.refresh();
	                }
        	},
        	initPics(){
        		if(this.seller.pics){
				      let picWidth=120;
				      let margin=6;
				      let width=(picWidth+margin)*this.seller.pics.length-margin; 	
				  	  this.$refs.picList.style.width=width+'px';
				  	  this.$nextTick(()=>{
				  	  	if(!this.picScroll){
				  	  		this.picScroll=new BScroll(this.$refs.picWrapper,{
				  	  		scrollX:true,
				  	  		eventPassthrough:'vertical'
				  	  		})
				  	  	}else{
				  	  		this.picScroll.refresh();
				  	  	}
				  	  })
				  }
        	}
        },
        components:{
        	star,
        	split
        }
    }
</script>

<style rel="stylesheet" lang="scss">
@import "../css/base.scss";
    .seller{
    	position: absolute;
    	top: 174px;
    	bottom: 0;
    	left: 0;
    	width: 100%;
    	overflow: hidden;
    	.overview{
			padding: 18px;
			position: relative;
			
			.title{
				line-height:14px;
				color:rgb(7,17,27);
				font-size:14px;
				margin-bottom:8px;
			}
			.desc{
				padding-bottom:18px;
				@include border-1px(rgba(7,17,27,0.1));
				font-size:0;
				.star{
					display:inline-block;
					vertical-align:top;
					margin-right:8px;

				}
				.text{
					display:inline-block;
					vertical-align:top;
					line-height:18px;
					margin-right:12px;
					color:rgb(77,85,93);
					font-size:10px;
				}


			}
			.remark{
				display:flex;
				padding-top:18px;

				.block{
					flex:1;
					text-align:center;
					border-right:1px solid rgba(7,17,27,0.1);
					&:last-child{
						@include border-none;

					}
					h2{
						margin-bottom:4px;
						line-height:10px;
						font-size:10px;
						color:rgb(147,153,159);

					}
					.content{
						line-height:24px;
						font-size:10px;
						color:rgb(7,17,27);
						.stress{
							font-size:24px;
						}
					}
				}
			}
			.favorite{
				position: absolute;
				right:11px;
				top:18px;
				text-align:center;
				width:50px;
				.icon-favorite{
					display:block;
					margin-bottom:4px;
					color:#d4d6d9;
					line-height:24px;
					font-size:24px;
					&.active{
						color:rgb(240,20,20);
					}

				}
				.text{
					line-height:10px;
					font-size:10px;
					color:rgb(77,85,93);
				}
			}
    	}
    	.bulletin{
    		padding:18px 18px 0 18px;
    		.title{
    			margin-bottom:8px;
    			line-height:14px;
    			color:rgb(7,17,27);
    			font-size:14px;
    		}
    		.content-wrapper{
    			padding:0 12px 16px 12px;
    			@include border-1px(rgba(7,17,27,0.1));
    			.content{
    				line-height:24px;
    				font-size:12px;
					color: rgb(240, 20, 20);
    			}

    		}
    		.supports{
    			.support-item{
    				padding:16px 12px;
    				font-size:0;
    				@include border-1px(rgba(7,17,27,0.1));
    				font-size:0;
    				&:last-child{
    					@include border-none;
    				}
    			}
    			.icon{
    				display:inline-block;
                    vertical-align:top;
					width:16px;
					height:16px;
					margin-right:6px;
					background-size:16px 16px;
					background-repeat:no-repeat;
					&.decrease{
						@include bg-images(seller,decrease_4);
					}
					&.discount{
						@include bg-images(seller,discount_4);
					}
					&.guarantee{
						@include bg-images(seller,guarantee_4);
					}
					&.invoice{
						@include bg-images(seller,invoice_4);
					}
					&.special{
						@include bg-images(seller,special_4);
					}
    			}
    			.text{
    				line-height:16px;
    				font-size:12px;
    				color:rgb(7,17,27);

    			}
    		}
    	}
    	.pics{
    		padding:18px;
    		.title{
    			line-height:14px;
				color:rgb(7,17,27);
				font-size:14px;
				margin-bottom:12px;
    		}
    		.pic-wrapper{
    			width:100%;
    			overflow:hidden;
    			white-space:nowrap;
    			.pic-list{
    				font-size:0;
    				.pic-item{
    					display:inline-block;
    					margin-right:6px;
    					width:120px;
    					height:90px;
    					&:last-child{
    						margin:0;

    					}
    				}
    			}
    		}
    	}
    	.info{
    		padding:18px 18px 0 18px;
    		.title{
    			padding-bottom:12px;
    			line-height:14px;
    			@include border-1px(rgba(7,17,27,0.1));
    			color:rgb(7,17,27);
    			font-size:14px;
    		}
    		.info-item{
    			padding: 16px 12px;
    			line-height: 16px;
    			@include border-1px(rgba(7,17,27,0.1));
    			font-size:12px;
    			&:last-child{
    				@include border-none;
    			}
    		}

    	}



    }
</style>


