<template>
    	<div class="ratings" ref="ratings">
    		<div class="ratings-content">
    			<div class="overview">
    				<div class="overview-left">
    					<h1 class="score">
    						{{seller.score}}
    					</h1>
    					<div class="title">
    						综合评分
    					</div>
    					<div class="rank">
    						高于周边商家{{seller.rankRate}}%
    					</div>
    				</div>
    				<div class="overview-right">
    					<div class="score-wrapper">
    						<span class="title">
    							服务态度
    						</span>
    						<star :size="36" :score="seller.serviceScore"></star>
    						<span class="score">{{seller.serviceScore}}</span>
    					</div>
    					<div class="score-wrapper">
    						<span class="title">
    							商品评分
    						</span>
    						<star :size="36" :score="seller.foodScore"></star>
    						<span class="score">{{seller.foodScore}}</span>
    					</div>
    					<div class="delivery-wrapper">
    						<span class="title">送达时间</span>
							<span class="delivery">{{seller.deliveryTime}}分钟</span>
    					</div>
    				</div>
    			</div>
    			<split></split>
    			<ratingSelect  @ratingtype="selectRating" @toggle="toggleContent" @countToggle="countToggle" :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="ratings" v-if="ratings"></ratingSelect>
    			<div class="rating-wrapper">
    				<ul>
    					<li v-for="rating in ratings" class="rating-item" v-show="needShow(rating.rateType,rating.text)">
    						<div class="avatar">
    							<img :src="rating.avatar" alt="" width="28" height="28">
    						</div>
    						<div class="content">
    							<h1 class="name">{{rating.username}}</h1>
    							<div class="star-wrapper">
    								<star :size="24" :score="rating.score"></star>
    								<span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
    							</div>
    							<p class="text">{{rating.text}}</p>
    							<div class="recommend" v-show="rating.recommend && rating.recommend.length">
    								<span class="icon-thumb_up"></span>
    								<span class="item" v-for="item in rating.recommend">{{item}}</span>
    							</div>
    							<div class="time">
    								{{rating.rateTime | formatDate}}
    							</div>
    						</div>
    						
    					</li>
    				</ul>
    			</div>
    		</div>
	   </div>
   
</template>
<script type="ecmascript-6">
    import BScroll from "better-scroll";
    import {formatDate} from "../js/date.js";
	import star from "./star.vue";
    import {mapState} from 'vuex';
    import ratingSelect from './ratingSelect.vue';
    import split from './split.vue';
    export default{
    	props:['foodCont'],
    	data(){
            return{
                selectType:2,
                onlyContent:true,
                desc:['全部','推荐','吐槽'],
                seller:'',
                ratings:''
            }
        },
    	mounted(){
    		this.seller=this.foodCont.seller;
    		this.ratings=this.foodCont.ratings;
    		this.$nextTick(() => {
		           this.initScroll();
		          });
    	},
    	filters:{
    		formatDate(date){
    			return formatDate(date);
    		}
    	},
    	methods:{
    		initScroll(){
        		if(!this.scroll){
	                this.scroll = new BScroll(this.$refs.ratings, {
	                  click: true,
	                })
	              }else{
	                	this.scroll.refresh();
	                }
        	},
    		countToggle(msg){
                this.onlyContent=!msg;
            },
    		needShow(type,text){
                if(this.onlyContent && !text){
                    return false;
                }
                if(this.selectType ==2){
                    return true;
                }else{
                    if(type==this.selectType){
                        return true;
                    }else{
                        return false;
                    }
                }
            },
    		 selectRating(type) {
		        this.selectType = type;
		        
		      },
		      toggleContent() {
		        this.onlyContent = !this.onlyContent;
		      }
    	},
    	components:{
    		star,
    		split,
    		ratingSelect
    	}
    }
</script>

<style rel="stylesheet" lang="scss">
@import "../css/base.scss";
    .ratings{
    	position: absolute;
		top:174px;
		left:0;
		width:100%;
		overflow:hidden;
		height:100%;
		.overview{
			display:flex;
			padding:18px 0;
			.overview-left{
				flex:0 0 137px;
				padding:6px 0;
				width:137px;
				border-right:1px solid rgba(7,17,27,0.1);
				text-align:center;
				@media only screen and(max-width:320px){
					flex:0 0 120px;
					width:120px;

				}
				.score{
					line-height:28px;
					font-size:24px;
					color:rgb(255,153,0);
					margin-bottom:6px;
				}
				.title{
					margin-bottom:8px;
					line-height:12px;
					font-size:12px;
					color:rgb(7,17,27);
				}
				.rank{
					line-height:10px;
					font-size:10px;
					color:rgb(147,153,159);
				}
			}
			.overview-right{
				flex:1;
				padding:5px 24px;
				.score-wrapper{
					margin-bottom:8px;
					font-size:0;
					.title{
						display:inline-block;
						font-size:12px;
						color:rgb(7,17,27);
						line-height:18px;
						vertical-align:top;

					}
					.star{
						display:inline-block;
						vertical-align:top;
						margin:0 12px;
					}
					.score{
						display:inline-block;
						vertical-align:top;
						margin:0 12px;
						color:rgb(255,153,0);
						font-size:12px;
					}
				}
				.delivery-wrapper{
					font-size:0;
					.title{
						font-size:12px;
						color:rgb(7,17,27);                              
						line-height:18px;
					}
					.delivery{
						font-size:12px;
						margin-left:12px;
						color:rgb(147,153,159);
					}
				}

			}
		}
		.rating-wrapper{
			padding:0 18px;
			.rating-item{
				display:flex;
				padding:18px 0;
				@include border-1px(rgba(7,17,27,0.1));
				.avatar{
					flex:0 0 28px;
					width:28px;
					margin-right:12px;
					img{
						border-radius:50%;
					}
					
				}
				.content{
						position:relative;
						flex:1;
						.name{
							line-height:12px;
							font-size:10px;
							color:rgb(7,17,27);
							margin-bottom:4px;
						}
						.star-wrapper{
							margin-bottom:6px;
							font-size:0;
							.star{
								display:inline-block;
								vertical-align:top;
								margin-right:6px;

							}
							.delivery{
								display:inline-block;
								vertical-align:top;
								line-height:12px;
								font-size:10px;
								color:rgb(147,153,159);
							}
						}
						.text{
							line-height:18px;
							color:rgb(7,17,27);
							font-size:12px;

						}
						.recommend{
							line-height:16px;
							font-size:0;
							.icon-thumb_up,.item{
								display:inline-block;
								margin:0 8px 4px 0;
								font-size:12px;
							}
							.icon-thumb_up{
								color:rgb(0,160,220);
							}
							.item{
								padding:0 6px;
								border:1px solid rgba(7,17,27,0.1);
								border-radius:1px;
								color:rgb(147,153,159);
								background:#fff;
							}
							
						}
						.time{
				            position: absolute;
				            top: 0;
				            right: 0;
				            line-height: 12px;
				            font-size: 10px;
				            color: rgb(147, 153, 159);
				        }
					}
			}
		}
    }
</style>


